import React from 'react';
import {NavLink} from 'react-router-dom';
import LanguageSelector from '../../components/translation/language-selector';

const Header = () => {
  return (
    <header>
      <nav>
        <LanguageSelector />
        <ul>
          <li>
            <NavLink exact={true} to="/admin/">Home 2</NavLink>
          </li>
          <li>
            <NavLink to="/admin/contact">contact 2</NavLink>
          </li>
          <li>
            <NavLink to="/admin/about">about 2</NavLink>
          </li>
        </ul>
      </nav>
    </header>
  );
};

export default Header;
